import React from 'react';
import PropTypes from 'prop-types';

class APSteps extends React.Component {
  constructor() {
    super();

    this.state = {
      currentStep: 0
    };
  }

  goToStep = currentStep => this.setState({ currentStep });
  goToNextStep = () =>
    this.setState({ currentStep: this.state.currentStep + 1 });
  goToPreviousStep = () =>
    this.setState({ currentStep: this.state.currentStep - 1 });

  render() {
    const { childComponents, ...otherProps } = this.props;
    const { currentStep } = this.state;
    const Component = childComponents[currentStep];

    return (
      <Component
        {...otherProps}
        goToStep={this.goToStep}
        goToNextStep={this.goToNextStep}
        goToPreviousStep={this.goToPreviousStep}
        currentStep={currentStep}
      />
    );
  }
}

APSteps.defaultProps = {
  childComponents: []
};

APSteps.propTypes = {
  childComponents: PropTypes.array
};

APSteps.contextTypes = {
  t: PropTypes.func.isRequired
};

export default APSteps;



