import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import APStepsComponent from '../common/APStepsComponent';
import DemoSettingsStaticComponent from './DemoSettingsStaticComponent';
import DemoSettingsEditableComponent from './DemoSettingsEditableComponent';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './DemoSettingsComponent.css';

const demoSettingsClasses = getBEMClasses('demo-settings-layout');

class DemoSettingsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  render() {
    return (
      <div className={demoSettingsClasses('main')}>
        <div className={demoSettingsClasses('info')} />
        <div className={demoSettingsClasses('body')}>
          <div className={demoSettingsClasses('page-header')}>
            <h1 className={demoSettingsClasses('title')}>
              {this.context.t('Demo Settings.')}
            </h1>
          </div>
          <APStepsComponent
            childComponents={[
              DemoSettingsStaticComponent,
              DemoSettingsEditableComponent
            ]}
            {...this.props}
          />
        </div>
      </div>
    );
  }
}

export default withRouter(DemoSettingsComponent);



