import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import DemoSettingsComponent from './DemoSettingsComponent';
import config from '../../config';

const DemoSettingsForm = reduxForm({
  form: 'demoSettings',
  enableReinitialize: true,
  onSubmit: (payload, dispatch) => {}
})(DemoSettingsComponent);

const mapStateToProps = state => {
  const { template, theme, chart } = config.global;
  const localConfig = JSON.parse(localStorage.getItem('apexTemplate'));
  return {
    initialValues: {
      template,
      theme,
      chart,
      ...localConfig
    },
    formValues: getFormValues('demoSettings')(state) || {}
  };
};

const DemoSettingsContainer = connect(mapStateToProps)(DemoSettingsForm);

export default DemoSettingsContainer;



