import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../common/APButton';
import APLabelWithText from '../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './DemoSettingsStaticComponent.css';
import ContentWithHeaderLayoutTemplate from '../common/LayoutTemplates/ContentWithHeaderLayoutTemplate/ContentWithHeaderLayoutTemplate';

const demoSettingsStaticClasses = getBEMClasses('demo-settings-static');
const demoSettingsWidgetsClasses = getBEMClasses('demo-settings-widget');

const getLayoutHeader = context => (
  <div className={demoSettingsWidgetsClasses('header')}>
    <div className={demoSettingsWidgetsClasses('header-text')}>
      {context.t('Settings')}
    </div>
  </div>
);

const DemoSettingsStaticComponent = (props, context) => {
  const { goToNextStep, formValues } = props;

  return (
    <ContentWithHeaderLayoutTemplate
      header={getLayoutHeader(context)}
      customClass={demoSettingsWidgetsClasses()}>
      <div className={demoSettingsStaticClasses()}>
        <div className={demoSettingsStaticClasses('row')}>
          <APLabelWithText
            label={context.t('Template')}
            text={formValues.template}
            customClass={demoSettingsStaticClasses()}
          />
        </div>
        <div className={demoSettingsStaticClasses('row')}>
          <APLabelWithText
            label={context.t('Color Theme')}
            text={formValues.theme}
            customClass={demoSettingsStaticClasses()}
          />
        </div>
        <div className={demoSettingsStaticClasses('row')}>
          <APLabelWithText
            label={context.t('Chart Type')}
            text={formValues.chart}
            customClass={demoSettingsStaticClasses()}
          />
        </div>

        <div className={demoSettingsStaticClasses('button-container')}>
          <APButton
            customClass={demoSettingsStaticClasses()}
            onClick={() => goToNextStep()}>
            {context.t('Edit')}
          </APButton>
        </div>
      </div>
    </ContentWithHeaderLayoutTemplate>
  );
};

DemoSettingsStaticComponent.defaultProps = {};

DemoSettingsStaticComponent.propTypes = {
  customClass: PropTypes.string,
  goToNextStep: PropTypes.func.isRequired
};

DemoSettingsStaticComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DemoSettingsStaticComponent;



